html, body {
  text-align: center;
  color: #fff;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

html {
  background-color: #002892;
}

body {
  padding-top: 40px;
}

a:link, a:visited {
  color: #fff;
}

h1, h2, h3 {
  font-weight: 500;
}

table {
  margin: 0 auto 15px;
  border-collapse: collapse;
}

td, th {
  padding: 5px;
}

tr {
  border-bottom: 1px solid #d8d8d8;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

#next-period {
  margin-bottom: 40px;
}